import React, { useState, useEffect, useContext } from "react";
import { Link } from "react-router-dom";
// import { Collapse } from 'react-collapse';

import { MdShoppingCart, MdSearch, MdMenu } from "react-icons/md";
import { FaUserCircle } from "react-icons/fa";
import * as S from "./styles";
import logo from "../../assets/logoPalcoweb.png";
import User from "../../Connections/user";
import Button from "../Button";
import { useHistory } from "react-router-dom";
import app from "../../auth/config";

import { AuthContext } from "../../auth/AuthContext";

function Header({ page, setPoupUp }) {
  const [select, setSelect] = useState(page);
  // const [isShown] = useState(false);
  const [logged, setLogged] = useState(false);
  const [toggle, setToggle] = useState(false);

  const pages = ["Inicio", "Categoria", "Canais", "Saiba Mais"];

  const history = useHistory();

  const pathFunction = (index) => {
    switch (index) {
      case 0:
        return "/";
      case 1:
        return "/categories";
      case 2:
        return "/canais";
      case 3:
        return "/SaibaMais";
      default:
        return "/";
    }
  };

  useEffect(() => {
    async function getUser() {
      const user = new User();
      const userInfo = await user.getUser();
      if (userInfo) {
        setLogged(true);
      }
    }
    getUser();
  }, []);

  const { currentUser } = useContext(AuthContext);

  const logado = localStorage.getItem("logado");
  const [perfilButton, setperfilButton] = useState(false);
  console.log("logado" + logado);

  if (currentUser) {
    return (
      <>
        {toggle && <S.ModalMenu></S.ModalMenu>}
        <S.Container>
          <S.Content>
            <img
              src={logo}
              alt="Logotipo Palcoweb"
              onClick={() => history.push("/")}
            />
            <S.Pages>
              {pages.map((page, index) => {
                return (
                  <S.Page
                    key={String(page)}
                    select={Number(index) === Number(select)}
                    onClick={() => setSelect(index)}
                  >
                    <Link to={pathFunction(index)}>{page}</Link>
                  </S.Page>
                );
              })}
            </S.Pages>

            <S.Icons>
              {logged ? (
                <FaUserCircle onClick={() => history.push(`/profile`)} />
              ) : (
                <>
                  {logado == "true" ? (
                    <>
                      {/* <MdShoppingCart onClick={() => history.push("/cart")} /> */}
                      <S.Perfil onMouseEnter={() => setperfilButton(true)}>
                        <S.Foto>
                          <i class="bi bi-person-fill"></i>
                        </S.Foto>
                        {perfilButton && (
                          <S.PerfilButton
                            onMouseOut={() => setperfilButton(false)}
                          >
                            <S.Button
                              onMouseEnter={() => setperfilButton(true)}
                              onClick={() => history.push(`/espetaculos`)}
                            >
                              Meus Favoritos
                            </S.Button>
                            <S.Button
                              onMouseEnter={() => setperfilButton(true)}
                              onClick={() => history.push(`/perfil`)}
                            >
                              Minha conta
                            </S.Button>
                            <S.Button
                              onMouseEnter={() => setperfilButton(true)}
                            >
                              Ajuda
                            </S.Button>
                            <S.Button
                              onMouseEnter={() => setperfilButton(true)}
                              onClick={() => {
                                localStorage.setItem("logado", "false");
                                app.auth().signOut();
                                history.push('/')
                              }}
                            >
                              Sair
                            </S.Button>
                          </S.PerfilButton>
                        )}
                      </S.Perfil>
                    </>
                  ) : (
                    <S.DoisBotons>
                      <Button
                        onClick={() => history.push(`/login`)}
                        text={"Entrar"}
                        color="black"
                        textColor="#FFF"
                      />

                      <Button
                        onClick={() => history.push(`/Assinatura`)}
                        text={"Assine ja"}
                      />
                    </S.DoisBotons>
                  )}
                </>
              )}
            </S.Icons>

            <S.Collapse>
              <MdMenu className="menu" onClick={() => setToggle(!toggle)} />
            </S.Collapse>
          </S.Content>
        </S.Container>
      </>
    );
  } else {
    return (
      <>
        {toggle && <S.ModalMenu></S.ModalMenu>}
        <S.Container>
          <S.Content>
            <img
              src={logo}
              alt="Logotipo Palcoweb"
              onClick={() => history.push("/")}
            />
            <S.Pages>
              {pages.map((page, index) => {
                return (
                  <S.Page
                    key={String(page)}
                    select={Number(index) === Number(select)}
                    onClick={() => setSelect(index)}
                  >
                    <Link to={pathFunction(index)}>{page}</Link>
                  </S.Page>
                );
              })}
            </S.Pages>

            <S.Icons>
              <S.DoisBotons>
                <Button
                  onClick={() => history.push(`/login`)}
                  text={"Entrar"}
                  color="black"
                  textColor="#FFF"
                />
                <Button
                  onClick={() => history.push(`/Assinatura`)}
                  text={"Assine ja"}
                />
              </S.DoisBotons>
            </S.Icons>

            <S.Collapse>
              <MdMenu className="menu" onClick={() => setToggle(!toggle)} />
            </S.Collapse>
          </S.Content>
        </S.Container>
      </>
    );
  }
}

export default Header;
