import React, { useCallback, useContext } from "react";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import * as S from "./styles";
import app from "../../auth/config"
import { AuthContext } from "../../auth/AuthContext"
import { useHistory } from "react-router-dom";
import './style.css'
import firebase from "firebase"

function Login() {
  let history = useHistory();
  const handleLogin = useCallback( async (event) => {
      event.preventDefault();
      const { email, password } = event.target.elements;
      try {
        await app
          .auth()
          .signInWithEmailAndPassword(email.value, password.value);
        history.push("/");
      } catch (error) {
        if (error == "TypeError: Cannot read property 'push' of undefined") {
          alert("Usuário ou senha inválido!");
        }
      }
    
  }, [history])

  const { currentUser } = useContext(AuthContext);

  if (currentUser) {
    localStorage.setItem("logado", 'true');
    history.push(`/`)
    return (
      <>

        <h1>Usuário Logado!</h1>
        <button onClick={() => app.auth().signOut()}>Sign out</button>

      </>);
  } else {
     
  }

  function loginFacebook() {
    var provider = new firebase.auth.FacebookAuthProvider();
    firebase
      .auth()
      .signInWithPopup(provider)
      .then((result) => {
        /** @type {firebase.auth.OAuthCredential} */
        var credential = result.credential;

        // The signed-in user info.
        var user = result.user;

        // This gives you a Facebook Access Token. You can use it to access the Facebook API.
        var accessToken = credential.accessToken;

        // ...
      })
      .catch((error) => {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        // The email of the user's account used.
        var email = error.email;
        // The firebase.auth.AuthCredential type that was used.
        var credential = error.credential;

        // ...
       
      });
  }

  function loginGoogle() {
    var provider = new firebase.auth.GoogleAuthProvider();
    firebase.auth()
      .signInWithPopup(provider)
      .then((result) => {

      }).catch((error) => {

        alert(error)
      });

  }
  return (


    <>

      <Header />
      <S.Container>
        <S.ContainerItem className="pt-5"><S.Span size="25">Minha conta</S.Span></S.ContainerItem>
        <form onSubmit={handleLogin}>
          <S.ContainerItem>
            <input className="ipt" name="email" type="email" placeholder="Email" />

          </S.ContainerItem>
          <S.ContainerItem>

            <input className="ipt" name="password" type="password" placeholder="Senha" />
          </S.ContainerItem>
          <S.ContainerItem className="pt-3">
            <S.Entrar className="btn m-2 ">Entrar</S.Entrar>
            <S.Cadastro className="btn m-2" onClick={() => history.push(`/Cadastro`)}> Cadastre-se</S.Cadastro>
          </S.ContainerItem>
        </form>

        <S.Span size="15"  className="p-3 ponte" onClick={() => history.push('/EsqueciSenha')}>Esqueceu a senha?</S.Span>

        <S.Facebook onClick={loginFacebook}> <i class="bi bi-facebook "></i> Entrar com Facebook</S.Facebook>
        <S.Google onClick={loginGoogle}><i class="bi bi-google"></i> Entrar com Google</S.Google>
      </S.Container>

      <Footer />



    </>


  );
}

export default Login;
