import React, { useState, useRef, useEffect } from "react";

import * as S from "./styles";


// import RoundedImage from '../../components/RoundedImage';
// import image from '../../assets/clarisse.png'


import Header from "../../components/Header";
import Footer from "../../components/Footer";
import StripeContainer from "../../components/Strip/StripContainer";

import { useHistory } from "react-router-dom";



function Pagamento() {

  const [temItem, setTem] = useState(true)

  function virificar() {
    if (temItem) {
      setTem(false)
    } else {
      setTem(true)
    }
  }


  const history = useHistory()
  return (
    <>
      <Header />
      <S.Container>
        <S.Span size="40">Pagamento</S.Span>
        <StripeContainer/>
      </S.Container>

      <Footer />
    </>
  );
}

export default Pagamento;
