import React, { useState, useEffect, useContext } from "react";

import { SwiperSlide } from "swiper/react";
import firebase from "firebase";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import Banner from "../../components/Banner";
import Section from "../../components/Section";
import Card from "../../components/Card";
import HorizontalList from "../../components/HorizontalList";
import * as S from "./styles";
import Auth from "../../components/popups/Auth";
import RegisterPopup from "../../components/popups/Register";
// import Welcome from "../../components/popups/Welcome";
import Info from "../../components/popups/Info";
// import Store from "../../Connections/store"

import lista from "./lista";
import listaCanais from "./listaCanais";
import { AuthContext } from "../../auth/AuthContext";
//import { BsHeartFill, BsFillGearFill } from "react-icons/bs";
import { useHistory } from "react-router-dom";
import TelaCardezao from "../Cardezao/index";

//share
import Share from '../../components/CardCompartilhar/index'


function Home() {
  const history = useHistory();
  // const store = new Store();
  const { currentUser } = useContext(AuthContext);
  // const channel = new Channel();
  const [visible, setVisible] = useState({ status: false, text: "" });
  const [cards, setCards] = useState([]);

  // useEffect(() => {
  //   getContent();
  // }, []);

  // const getContent = () => {
  //   const received = channel.getContent();
  //   setCards(received.data);
  // };

  const changeVisible = (value) => {
    if (value === "off") {
      setVisible({
        status: false,
        text: value,
      });
    } else {
      setVisible({
        status: true,
        text: value,
      });
    }
  };

  const changeToShow = () => {
    if (visible.text === "Enter") {
      return <Auth visible={visible} changeVisible={changeVisible} />;
    }
    if (visible.text === "Register") {
      return <RegisterPopup visible={visible} changeVisible={changeVisible} />;
    }
    if (visible.text === "Info") {
      return <Info visible={visible} changeVisible={changeVisible} />;
    }
  };


  const card = cards.length < 5 ? cards.concat(["", "", "", "", ""]) : cards;

  const ListaTeste = lista;
  const [abriu, setAbri] = useState(false);
  const [conteudoCard, setconteudoCard] = useState(false);
  const [imgUrl, setImgUrl] = useState("");
  const [link, setLink] = useState("");
  const [cardTitle, setcardTitle] = useState("");
  const [sinopsia, setsinopsia] = useState("");
  const [artista, setartista] = useState("");
  const [categoria, setcategoria] = useState("");
  const [classificacao, setclassificacao] = useState("");
  const [descricaoClassifica, setdescricaoClassifica] = useState("");
  const [elenco, setelenco] = useState("");
  const [producao, setproducao] = useState("");
  const [direcao, setdirecao] = useState("");
  const [duracao, setduracao] = useState("");
  const [compartilhar, setcompartilhar] = useState(false);

  const ListaCanais = listaCanais
  function showConteudo(
    url,
    link,
    title,
    sinopsia,
    artista,
    categoria,
    classificacao,
    descricaoClassifica,
    elenco,
    producao,
    direcao,
    duracao
  ) {
    if (abriu) {
      setAbri(false);
      setImgUrl("");
      setLink("");
      setcardTitle("");
    } else {
      setAbri(true);
      setImgUrl(url);
      setLink(link);
      setcardTitle(title);
      setsinopsia(sinopsia);
      setartista(artista);
      setcategoria(categoria);
      setclassificacao(classificacao);
      setdescricaoClassifica(descricaoClassifica);
      setelenco(elenco);
      setproducao(producao);
      setdirecao(direcao);
      setduracao(duracao);
    }
  }

  return (
    <>
      {visible.status ? (
        <>{changeToShow(visible.text)}</>
      ) : (
        <>
          <Header page={0} />
          {abriu ? (
            <TelaCardezao
              link={link}
              direcao={direcao}
              producao={producao}
              elenco={elenco}
              voltar={showConteudo}
              artista={artista}
              sinopsia={sinopsia}
              descricaoClassifica={descricaoClassifica}
              classificacao={classificacao}
              img={imgUrl}
              titulo={cardTitle}
              artista={artista}
              categoria={categoria}
              duracao={duracao}
            ></TelaCardezao>
          ) : (
            <>
              <Banner />
              <S.Container>
                <Section title="Lançamentos" filter>
                  <HorizontalList id="emAlta">
                    {ListaTeste.map((card, index) => {
                      return (
                        <SwiperSlide key={String(index)}>
                          {card === "" ? (
                            <Card noContent />
                          ) : (
                            <S.CardTest>
                              <S.Img
                                src={card.thumbnail}
                                style={{
                                  position: "absolute",
                                  width: 250,
                                  height: 400,
                                }}
                                value={card.key}
                              />

                              <S.InfoCard>
                                <S.InfoCardConteudo>
                                  <S.Classificacao>
                                    {card.classificacao}
                                  </S.Classificacao>
                                  <S.Coracao
                                    onClick={() => {
                                      if (currentUser) {
                                        if(localStorage.getItem("fav")!==null){
                                          var fav = JSON.parse(localStorage.getItem("fav")).concat(card.title)
                                          localStorage.setItem("fav", JSON.stringify(fav));
                                        } else {
                                          var fav = [card.title]
                                          localStorage.setItem("fav", JSON.stringify(fav));
                                        }
                                      } else {
                                        history.push("login");
                                      }
                                    }}
                                  >
                                    <i className="bi bi-suit-heart-fill"></i>
                                  </S.Coracao>

                                  <S.Compartilhar >
                                    <i className="bi bi-share-fill"  onMouseEnter={() =>  setcompartilhar(true)}></i>
                                    {compartilhar && <Share sair={setcompartilhar}></Share>}
                                  </S.Compartilhar>
                                </S.InfoCardConteudo>
                                
                                <S.Titulo>{card.title}</S.Titulo>
                                <S.ConteudoCard>
                                  Com {card.Artista}
                                </S.ConteudoCard>
                                <S.ConteudoCard>
                                  <i className="bi bi-star-fill"></i>{" "}
                                  <i className="bi bi-star-fill"></i>{" "}
                                  <i className="bi bi-star-fill"></i>
                                  <i className="bi bi-star-fill"></i>
                                </S.ConteudoCard>
                                <S.ConteudoCard>
                                  {card.categoria}
                                </S.ConteudoCard>
                                <S.Sinopsia>{card.synopsis}</S.Sinopsia>

                                <S.ButtonAcessar
                                  className="btn btn-warning"
                                  onClick={() => {
                                    showConteudo(
                                      card.thumbnail,
                                      card.link,
                                      card.title,
                                      card.synopsis,
                                      card.Artista,
                                      card.categoria,
                                      card.classificacao,
                                      card.descricaoClassifica,
                                      card.elenco,
                                      card.producao,
                                      card.direcao,
                                      card.duracao
                                    );
                                    document.getElementById("root").scroll(0, 0)
                                  }
                                  }
                                >
                                  Saiba Mais{" "}
                                </S.ButtonAcessar>
                              </S.InfoCard>
                            </S.CardTest>
                          )}
                        </SwiperSlide>
                      );
                    })}
                  </HorizontalList>
                </Section>

                <Section title="Em alta" filter>
                  <HorizontalList id="recomendados">
                    {card.map((card, index) => {
                      return (
                        <SwiperSlide key={String(index)}>
                          {card === "" ? (
                            <Card noContent />
                          ) : (
                            <Card index={index} info={card} />
                          )}
                        </SwiperSlide>
                      );
                    })}
                  </HorizontalList>
                </Section>

                <Section title="Canais" filter>
                  <HorizontalList id="canais">
                    {ListaCanais.map((card, index) => {
                      return (
                        <SwiperSlide key={String(index)}>
                          {card === "" ? (
                            <Card noContent />
                          ) : (
                            <S.CardCanais>
                              <S.Img
                                src={card.link}
                                style={{
                                  position: "absolute",
                                  width: 250,
                                  height: 250,
                                  borderRadius: "50%",
                                }}
                                value={card.key}
                              />
                            </S.CardCanais>
                          )}
                        </SwiperSlide>
                      );
                    })}
                  </HorizontalList>
                </Section>
              </S.Container>
              <Footer />
            </>
          )}
        </>
      )}
    </>
  );
}

export default Home;
