import React, { useState, useRef, useEffect } from "react";

import * as S from "./styles";

import { SwiperSlide } from "swiper/react";

import BaseScreen from "../../components/BaseScreen";
import HorizontalList from "../../components/HorizontalList";
import Card from "../../components/Card";
import Section from "../../components/Section";
import Processing from "../../components/Processing";
import CheckoutCard from "../../components/CheckoutCard";
// import RoundedImage from '../../components/RoundedImage';
// import image from '../../assets/clarisse.png'
import Store from "../../Connections/store";

import Channel from '../../Connections/channel';
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import { useHistory } from "react-router-dom";

function ConfirmCompra() {
  const store = new Store();
  const [temItem, setTem] = useState(true)
  const [Mes, setMes] = useState(0)
  const [Dia, setDia] = useState(0)
  const [Ano, setAno] = useState(0)
  const [Horas, setHoras] = useState(0)
  const [Minutos, SetMinutos] = useState(0)

  function virificar() {
    if (temItem) {
      setTem(false)
    } else {
      setTem(true)
    }
  }

  const now = new Date()

  const MesAtual = now.getMonth() + 1
  const DiaAtual = now.getDay() - 2
  const AnoAtual = now.getFullYear()
  const HoraAttual = now.getHours()
  const MinutoAtual = now.getMinutes()
  console.log(`data atual: ${DiaAtual} / ${MesAtual} / ${AnoAtual} Horario ${HoraAttual}:${MinutoAtual}`)

  const MesEspetaculo = 9
  const DiaEspetaculo = 4
  const AnoEspetaculo = 2021
  const HorarioEspetaculo = 19
  const MinutosEspetaculo = 30


  useEffect(() => {

    setInterval(function () {

      setMes(MesEspetaculo - MesAtual)

      if (DiaAtual > DiaEspetaculo) {
        setDia(DiaAtual - DiaEspetaculo)
      } else {
        setDia(DiaEspetaculo - DiaAtual)
      }


      if (HoraAttual > HorarioEspetaculo) {
        setHoras(HoraAttual - HorarioEspetaculo)
      } else {
        setHoras(HorarioEspetaculo - HoraAttual)
      }


      if (MinutoAtual > MinutosEspetaculo) {
        SetMinutos(MinutoAtual - MinutosEspetaculo)
      } else {
        SetMinutos(MinutosEspetaculo - MinutoAtual)
      }


    }, 1000)


  }, [Dia, Minutos, Horas])

  const history = useHistory()
  return (
    <>
      <Header />

      <S.Container>

        <S.Sessao>
          <S.Span size="30" className="p-5">Agradecemos sua compra</S.Span>
          <S.Span size="15" className="p-2">Sua sessão começã em:</S.Span>

          <S.SessaoItem>
            <S.Dias><S.Span size="50" cor="#E4B810">{Dia}</S.Span></S.Dias>
            <S.Horas ><S.Span size="50" cor="#E4B810">{Horas}</S.Span></S.Horas>
            <S.Minutos><S.Span size="50" cor="#E4B810">{Minutos}</S.Span></S.Minutos>
          </S.SessaoItem>

          <S.SessaoItem>
            <S.Span size="15" className="p-3">Dias</S.Span> <S.Span size="15" className="p-3">Horas</S.Span>  <S.Span size="15" className="p-3">Minutos</S.Span>
          </S.SessaoItem>


          <S.SessaoItem>
            <S.Span size="15" className="p-5">Você vai receber uma confirmação de pagamento por SMS</S.Span>
          </S.SessaoItem>




        </S.Sessao>

        <S.Carrinho>
          <S.Img></S.Img>





          <S.Info>
            <S.Span size="30" >Titulo</S.Span>
            <S.Span size="20" >Com ...</S.Span>
            <S.Span size="20" className="pt-2">data ...</S.Span>
            <S.Span size="20 ">Horarios</S.Span>
          </S.Info>

          <S.CarrinhoItem>
            <S.Span size="20">Backstage</S.Span>
            <S.Span size="20">Total</S.Span>


          </S.CarrinhoItem>



        </S.Carrinho>


        <S.BordarBottom></S.BordarBottom>
        <S.AreaCancelar><S.Span size="15" className="p-1" cor="#E4B810">Cancelar Compra</S.Span></S.AreaCancelar>

        <S.AreaButtons>
          <S.Button className="btn" onClick={() => history.push(`/`)}><S.Span size="20" cor="#FFF" > <i class="bi bi-chevron-left"></i>Voltar para o inicio</S.Span></S.Button>
          <S.ButtonContinuar className="btn" onClick={() => history.push(`/espetaculos/idespetaculo`)}>Meus espetaculos</S.ButtonContinuar>
        </S.AreaButtons>

      </S.Container>
      <Footer />
    </>
  );
}

export default ConfirmCompra;
