import styled from 'styled-components';

import { SCREEN_SIZES } from '../../utils/screen';

export const Container = styled.header`
  position: fixed;
  width:100%;
  height: 90px;
  background: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index:10;
  h1{
    color: #fff;
  }
  .search{
    position: relative;
    right:0.8vw;
    }
`;

export const Content = styled.div`
  position:relative;
  width: 90%;
  display: flex;

  align-items: center;

  img{
    cursor: pointer;
    transition: opacity 0.3s;
    :hover{
      opacity: 0.8;
    }
  }

  @media (max-width: ${SCREEN_SIZES.sm}) {
    width: 90%;
    display: flex;

    svg{
      font-size: 25px;
      color: ${({ color, theme }) => color ? `${theme.palette.primary.main}` : '#fff'};
      margin-right: 20px;
     
      cursor: pointer;

      transition: all 0.2s;

      &:hover {
        color: ${({ theme }) => theme.palette.primary.main};
      }
    }
  }
`

export const Pages = styled.div`
  display: flex;
  height: 100%;
  margin-left: 50px;
  @media (max-width: ${SCREEN_SIZES.sm}) {
    display: none;
  }
  width: 90%;
`

export const Page = styled.span`
  font-weight: bold;
  font-size: 14px;
  margin-right: 44px;

  a {
    color: ${({ select, theme }) => select ? `${theme.palette.primary.main}` : '#fff'};
    cursor: pointer;
    text-decoration: none;
    transition: all 0.2s;

    &:last-child{
      margin-right: 0;
    }

    &:hover {
      color: ${({ theme }) => theme.palette.primary.main};
    }
  }
`;

export const Icons = styled.div`
  display: flex;
  gap:0.5vw;

  @media (max-width: ${SCREEN_SIZES.sm}) {
    svg {
      display: none;
      }
  }

  input {
    margin-right: -30px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 10px;
    background: transparent;
    border: 1px white solid;
    outline: none;
    color: white;
    border-radius: 5px;
    margin-left: 60px;
   
    ::-webkit-input-placeholder {
     color:white;
    }
  }

  svg {
    font-size: 25px;
    color: ${({ color, theme }) => color ? `${theme.palette.primary.main}` : '#fff'};
    margin-right: 20px;
 margin: auto;
    cursor: pointer;

    transition: all 0.2s;

    &:hover {
      color: ${({ theme }) => theme.palette.primary.main};
    }

    
  }

  svg.search {
    margin:auto;
    color: white;
    margin-right: 20px;
  }  

`;

export const Collapse = styled.div`
  display: flex;
  flex-direction: column;
  height: auto;
  color: #fff;
  z-index: 11;

  @media (min-width: ${SCREEN_SIZES.sm}) {  
    display: none;
    color: #fff;

    svg {
      font-size: 25px;
      color: ${({ color, theme }) => color ? `${theme.palette.primary.main}` : '#fff'};
      margin-right: 20px;
  
      cursor: pointer;

      transition: all 0.2s;

      &:hover {
        color: ${({ theme }) => theme.palette.primary.main};
      }
    }
  }
`;

export const ModalMenu = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  height: 100%;
  background: #0001;
`;

export const InputBuscar = styled.input`
  position:relative;
  height: 6vh;
  width: 20vw;
  top: 1.2vh;
  background-color: black;
  border: 1px solid white;
  color: white;
`

export const DoisBotons = styled.div`
display: flex ;
flex-direction: row;
width: 200px;
justify-content: space-around;
`

export const Perfil = styled.button`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  border-radius:12px;
  padding: 20px;
  margin: 10px;
  background-color: white;
  border: none;
  color: black;
  :hover{
    background-color: #FDCC12;
  }


`

export const Foto = styled.span`

  width: 25px;
  height: 25px;
 

`

export const PerfilButton = styled.div`
  
  position: absolute;
  background-color: white;
  margin-top: 290px;
  width: 10%;
  height: 200px;



`

export const Button = styled.button`

 width: 100%;
 height: 30%;
 border:none; 
 background-color: #1B1B1B;
 color: white;
 :hover{
   background-color: #FDCC12;
   color: black;
 }
`