export const Titulo = styled.span`
  margin: auto;
  margin-top: 5px;
  margin-bottom: 5px;
font-size: 23px;
`



export const ConteudoCard = styled.span`
  margin: auto;
  margin-top: 5px;
  margin-bottom: 5px;
  font-size: 15px;
`



export const Sinopsia = styled.span`
 margin: auto;
margin:10px;
font-size: 15px;
overflow: hidden;
`


export const ButtonAcessar = styled.button`
 margin: auto;
color: white;
font-size: 15px;
width: 150px;
text-transform: uppercase;
font-weight: bold;
:hover{
  background-color: white;
  color: black;
  border-color: white;

}
`

export const SobrePosto = styled.div`
position: relative;
width: 250px;
height: 400px;
display: flex;

background-color: rgba(0, 0, 0, 1) ;
z-index: 1;
`



export const CardCanais = styled.div`
 width:250px;
 height: 250px;
  background-image: url(${props => props.urlimg});
  background-color: white;
  
  justify-content: center;
  align-items: center;
  border-radius: 50%;
   margin: 40px;
`