import React, { useState, useContext } from "react";
import {useParams} from "react-router-dom";

import { AuthContext } from "../../auth/AuthContext";
import * as S from "./styles";
import firebase from "firebase"

// import RoundedImage from '../../components/RoundedImage';
// import image from '../../assets/clarisse.png'
import Store from "../../Connections/store";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import { useHistory } from "react-router-dom";
import './style.css'



function Cart() {
  
  const { currentUser } = useContext(AuthContext);
  const history = useHistory();
  if(!currentUser){
    history.push('login')
  }
  const store = new Store();
  const [temItem, setTem] = useState(true);
  var db = firebase.firestore();


  function urlParams() {
    let url = window.location.href;
    url = url.split("?");
    if (url[1] !== undefined) {
      let params = url[1].split("&");
      let par1 = params[0],
        par2 = params[1];
      params[0] = par1.substring(3).replaceAll("%20", " ").replaceAll('0', '')
      params[1] = par2.substring(9)
      return params;
    } else {
      return false;
    }
  }
  console.log();
  //  ----->>>>
  //  ----->>>>
  //  Retorna dois objetos, id e horario vindos direto da url
  //  ----->>>>
  //  ----->>>>

  const teste = JSON.stringify(db.collection("lista").doc("title").get());
  console.log(teste);


  //  ----->>>>
  //  ----->>>>
  //  Retorna um json, se quiser receber a string, é só  tirar a função "JSON.stringify()" da parada
  //  ----->>>>
  //  ----->>>>
  
  try {
    let UserID = firebase.auth().currentUser.uid;
    var json = {
      titulo: urlParams()[0],
      hora: urlParams()[1],
    };
    firebase
      .database()
      .ref(UserID + "/cart/")
      .update({
        json,
      });
  } catch (error) {
  }
  var docRef = firebase.database().ref();
  let global
  let items = docRef.child(firebase.auth().currentUser.uid).child("cart").get().then( async (snapshot) => {
    await (global = snapshot.val())

    return json;
  });

  items = Promise.all([items])
  items.then( (array) => {
    json = JSON.stringify(array)
  })

  let lista 
  // setTimeout(() => {
  //   firebase.firestore().collection("lista").where(global.json.titulo, "==", true).get().then(async (snapshot) =>{
  //     await snapshot.val()
  //   })    

    
  // }, 2000);




  return (
    <>
      <Header />

      <S.Container>
        <h2>Carrinho de compras</h2>

        {temItem && (
          <S.Carrinho>
            <S.X>
              <S.Span size="20">
                <i class="bi bi-x-lg"></i>
              </S.Span>
            </S.X>

            {/* <S.Img></S.Img> */}
            <S.Info>
              <S.Span size="30" id="titulo">
                {setTimeout(() => {
                  const horario = () => {
                    let id = document.getElementById("titulo");
                    if (global.json.titulo!= undefined){
                      id.innerHTML = global.json.titulo;
                    } else {
                      id.innerHTML = ""
                    }
                  };
                  horario();
                }, 3000)}
              </S.Span>
              <S.Span size="35 "  id="horario">
                {setTimeout(() => {
                  const horario = () => {
                    let id = document.getElementById("horario");
                    if (global.json.hora!= undefined){
                      id.innerHTML = global.json.hora;
                    } else {
                      id.innerHTML = "carrinho vazio"
                    }
                  };
                  horario();
                }, 3000)}
              </S.Span>
            </S.Info>

            <S.CarrinhoItem>
              <S.Span size="20">Backstage</S.Span>
              <S.Span size="20">Total</S.Span>

              <S.ButtonContinuar
                className="btn btn-warning m-5"
                onClick={() => history.push(`/pagamento/idespetaculo`)}
              >
                {" "}
                Pagar
              </S.ButtonContinuar>
            </S.CarrinhoItem>
          </S.Carrinho>
        )}
      </S.Container>
      <Footer />
    </>
  );
}

export default Cart;
