import React, { useState, useEffect } from "react";
import { SwiperSlide } from "swiper/react";
import { useHistory } from "react-router";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import Section from "../../components/Section";
import Card from "../../components/Card";
import HorizontalList from "../../components/HorizontalList";
import * as S from "./styles";
//share
import Share from '../../components/CardCompartilhar/index'

// import Store from "../../Connections/store"
import TelaCardezao from '../Cardezao/index'
import lista from '../Home/lista'
import Horarios from "../Horarios";


function Cardezao(props) {
  const ListaTeste = lista
  const [visible, setVisible] = useState({ status: false, text: "" });
  const [conteudoCard, setconteudoCard] = useState(false)
  const [imgUrl, setImgUrl] = useState('')
  const [link, setLink] = useState('')
  const [cardTitle, setcardTitle] = useState('')
  const [sinopsia, setsinopsia] = useState('')
  const [artista, setartista] = useState('')
  const [categoria, setcategoria] = useState('')
  const [classificacao, setclassificacao] = useState('')
  const [descricaoClassifica, setdescricaoClassifica] = useState('')
  const [elenco, setelenco] = useState('')
  const [producao, setproducao] = useState('')
  const [direcao, setdirecao] = useState('')
  const [abriu, setAbri] = useState(false)
  const [abrirCard, setAbrirCardzao] = useState(false)
  const history = useHistory()

  const [compartilharFica, setcompartilharFicha] = useState(false);

  const [compartilhar, setcompartilhar] = useState(false);









  function showConteudo(url, link, title, sinopsia, artista, categoria, classificacao, descricaoClassifica, elenco, producao, direcao) {
    if (abrirCard) {
      setAbrirCardzao(false)
      setImgUrl('')
      setLink('')
      setcardTitle('')

    } else {

      setAbrirCardzao(true)
      setImgUrl(url)
      setLink(link)
      setcardTitle(title)
      setsinopsia(sinopsia)
      setartista(artista)
      setcategoria(categoria)
      setclassificacao(classificacao)
      setdescricaoClassifica(descricaoClassifica)
      setelenco(elenco)
      setproducao(producao)
      setdirecao(direcao)


    }

  }

  function showHorario() {
    if (abriu) {
      setAbri(false)
    } else {
      setAbri(true)
    }
  }

  function fechar() {
    setAbri(false)
  }
  // window.scroll({
  //   top:-50,
  //   behavior: 'smooth'
  // })


  const logado = localStorage.getItem("logado");

  if (logado != "true") {

    history.push("/login")

  }

  return (
    <>
      {abriu ? (
        <Horarios
          link={props.link}
          direcao={props.direcao}
          producao={props.producao}
          elenco={props.elenco}
          voltar={fechar}
          artista={props.artista}
          sinopsia={props.sinopsia}
          descricaoClassifica={props.descricaoClassifica}
          classificacao={props.classificacao}
          img={props.img}
          titulo={props.titulo}
          artista={props.artista}
          categoria={props.categoria}
        ></Horarios>
      ) : (
        <>
          {abrirCard ? (
            <TelaCardezao
              link={link}
              direcao={direcao}
              producao={producao}
              elenco={elenco}
              voltar={showConteudo}
              artista={artista}
              sinopsia={sinopsia}
              descricaoClassifica={descricaoClassifica}
              classificacao={classificacao}
              img={imgUrl}
              titulo={cardTitle}
              artista={artista}
              categoria={categoria}
            ></TelaCardezao>
          ) : (
            <>
              <Header onClick={window.scrollTo(0, 0)} />
              <S.Container>
                <S.Thumbnail src={props.img}></S.Thumbnail>

                <S.Conteudo>
                  <S.ButtonVoltar onClick={() => window.location.reload()}>
                    {" "}
                    <i className="bi bi-x-lg"></i>{" "}
                  </S.ButtonVoltar>

                  <S.HeaderConteudo>
                    <S.Item flexgrow={5}>
                      <S.Span>
                        {" "}
                        <S.Classificacao>
                          {props.classificacao}
                        </S.Classificacao>{" "}
                        <S.Span className="m-2">
                          {props.descricaoClassifica}
                        </S.Span>
                        
                      </S.Span>
                    </S.Item>
                    <S.Item flexgrow={1}>
                      <S.Coracao>
                        <i className="bi bi-suit-heart-fill"></i>
                      </S.Coracao>
                    </S.Item>
                    <S.Item flexgrow={1}>
                      <S.Compartilhar >
                        {" "}

                        <i className="bi bi-share-fill" onMouseEnter={() => setcompartilharFicha(true)}></i>
                        {compartilharFica && <Share sairficha={setcompartilharFicha}></Share>}
                      </S.Compartilhar>
                    </S.Item>
                  </S.HeaderConteudo>

                  <S.Informacoes>
                    <S.Item>
                      <S.Span size="22"> {props.titulo}</S.Span>
                    </S.Item>
                    <S.Item>
                      <S.Span size="15"> Com {props.artista}</S.Span>
                    </S.Item>
                    <S.Item>
                      <S.Span size="14">
                        {" "}
                        <i className="bi bi-star-fill"></i>{" "}
                        <i className="bi bi-star-fill"></i>{" "}
                        <i className="bi bi-star-fill"></i>{" "}
                        <i className="bi bi-star-fill"></i>
                        <i className="bi bi-star-fill"></i>
                      </S.Span>
                    </S.Item>
                    <S.Item>
                      <S.Span size="14" style={{ 'overflow': 'hidden' }}> {props.sinopsia}</S.Span>
                    </S.Item>
                    <S.Item>
                      <S.Span size="15"> Elenco: {props.elenco}</S.Span>
                    </S.Item>
                    <S.Item>
                      <S.Span size="15"> producao: {props.producao}</S.Span>
                    </S.Item>
                    <S.Item>
                      <S.Span size="15"> direcao: {props.direcao}</S.Span>
                    </S.Item>
                    <S.Item>
                      <S.Span size="15"> duração: {props.duracao}</S.Span>
                    </S.Item>
           
                    <S.Item>
                      <S.Span>
                        {/**    <S.ButtonHorario
                          className="btn btn-warning m-5 "
                          onClick={() =>
                            showHorario(
                              props.thumbnail,
                              props.link,
                              props.title,
                              props.synopsis,
                              props.Artista,
                              props.categoria,
                              props.classificacao,
                              props.descricaoClassifica,
                              props.elenco,
                              props.producao,
                              props.direcao
                            )
                          
                        >console
                          {" "}
                          Horarios{" "}
                        </S.ButtonHorario>}*/ }
                       <S.ButtonAcessar className="btn btn-warning m-5" onClick={() => {
                            if(localStorage.getItem("assistidos")!==null){
                              if(JSON.parse(localStorage.getItem("assistidos")).includes(props.titulo)){
                                alert("você já assistiu a esse espetáculo.")
                              } else {
                                var assistidos = JSON.parse(localStorage.getItem("assistidos")).concat([props.titulo])
                                localStorage.setItem("assistidos", JSON.stringify(assistidos));
                                window.location.href = props.link
                              }
                            } else {
                              localStorage.setItem("assistidos", JSON.stringify([props.titulo]))
                              window.location.href = props.link;
                            }
                          }
                        }>Assistir</S.ButtonAcessar>
                      </S.Span>
                    </S.Item>
                  </S.Informacoes>
                </S.Conteudo>
              </S.Container>
              <Section title="em alta" filter>
                <HorizontalList id="emAlta">
                  {ListaTeste.map((card, index) => {
                    return (
                      <SwiperSlide key={String(index)}>
                        {card === "" ? (
                          <Card noContent />
                        ) : (
                          <S.CardTest>
                            <S.Img
                              src={card.thumbnail}
                              style={{
                                position: "absolute",
                                width: 250,
                                height: 400,
                              }}
                              value={card.key}
                            />

                            <S.InfoCard>
                              <S.InfoCardConteudo>
                                <S.ClassificacaoCard>
                                  {card.classificacao}
                                </S.ClassificacaoCard>
                                <S.Coracao>
                                  <i className="bi bi-suit-heart-fill"></i>
                                </S.Coracao>

                                <S.Compartilhar>
                                  <i className="bi bi-share-fill" onMouseEnter={() => setcompartilhar(true)}></i>
                                  {compartilhar && <Share sair={setcompartilhar}></Share>}
                                </S.Compartilhar>
                              </S.InfoCardConteudo>

                              <S.Titulo>{card.title}</S.Titulo>
                              <S.ConteudoCard>
                                Com {card.Artista}
                              </S.ConteudoCard>
                              <S.ConteudoCard>
                                <i className="bi bi-star-fill"></i>{" "}
                                <i className="bi bi-star-fill"></i>{" "}
                                <i className="bi bi-star-fill"></i>
                                <i className="bi bi-star-fill"></i>
                              </S.ConteudoCard>
                              <S.ConteudoCard>
                                {card.categoria}
                              </S.ConteudoCard>
                              <S.Sinopsia>{card.synopsis}</S.Sinopsia>
                               
                                
                              <S.ButtonAcessar
                                className="btn btn-warning"
                                onClick={() =>
                                  showConteudo(
                                    card.thumbnail,
                                    card.link,
                                    card.title,
                                    card.synopsis,
                                    card.Artista,
                                    card.categoria,
                                    card.classificacao,
                                    card.descricaoClassifica,
                                    card.elenco,
                                    card.producao,
                                    card.direcao
                                  )
                                }
                              >
                                Saiba Mais{" "}
                              </S.ButtonAcessar>
                            </S.InfoCard>
                          </S.CardTest>
                        )}
                      </SwiperSlide>
                    );
                  })}
                </HorizontalList>
              </Section>
              {/*}
                    <Section title="Loja" filter>
                        <HorizontalList id="canais">
                            {ListaTeste.map((card, index) => {
                                return (
                                    <SwiperSlide key={String(index)}>
                                        {card === "" ? (
                                            <Card noContent />
                                        ) : (
                                            <S.Card >
                                                <S.Img src={'https://images.pexels.com/photos/7562644/pexels-photo-7562644.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940'} style={{ position: "absolute", width: 250, height: 250, }} value={card.key} />
                                            </S.Card>
                                        )}
                                    </SwiperSlide>
                                );
                            })}
                        </HorizontalList>
                    </Section>
                        {*/}

              <S.Produtora>
                <S.ImgProdutora
                  src={require("../../assets/logo-chaim.png")}
                ></S.ImgProdutora>
                <S.Item>
                  Chaim Entretenimento <br /> A Chaim Entretenimento é
                  responsável por promo- ver entretenimento de alta qualidade
                  e proporcio- nar ao público grandes espetáculos teatrais e
                  musi- cais. Com 20 anos de experiência, produzindo e
                  promovendo diversos espetáculos por todo o Brasil.
                </S.Item>
                <S.Item>CONHEÇA O CANAL </S.Item>
                <S.Item className="m-1">
                  {" "}
                  <i class="bi bi-chevron-right"></i>{" "}
                </S.Item>
              </S.Produtora>

              <Footer />
            </>
          )}
        </>
      )}
    </>
  );
}

export default Cardezao;
