import React, { useState, useEffect } from "react";
import { SwiperSlide } from "swiper/react";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import Section from "../../components/Section";
import Card from "../../components/Card";
import HorizontalList from "../../components/HorizontalList";
import * as S from "./styles";
import Auth from "../../components/popups/Auth";
import RegisterPopup from "../../components/popups/Register";
// import Welcome from "../../components/popups/Welcome";
import Info from "../../components/popups/Info";

// import Store from "../../Connections/store"
//import { BsHeartFill, BsFillGearFill } from "react-icons/bs";
import lista from "./lista";
import "./style.css";
import { useHistory } from "react-router-dom";

function Horarios(props) {
  const ListaTeste = lista;
  const [abriu, setAbri] = useState(false);
  const [visible, setVisible] = useState({ status: false, text: "" });
  const [conteudoCard, setconteudoCard] = useState(false);
  const [imgUrl, setImgUrl] = useState("");
  const [link, setLink] = useState("");
  const [cardTitle, setcardTitle] = useState("");
  const [sinopsia, setsinopsia] = useState("");
  const [artista, setartista] = useState("");
  const [categoria, setcategoria] = useState("");
  const [classificacao, setclassificacao] = useState("");
  const [descricaoClassifica, setdescricaoClassifica] = useState("");
  const [elenco, setelenco] = useState("");
  const [producao, setproducao] = useState("");
  const [direcao, setdirecao] = useState("");

  const [checado, setchecado] = useState(false);

  function showConteudo(
    url,
    link,
    title,
    sinopsia,
    artista,
    categoria,
    classificacao,
    descricaoClassifica,
    elenco,
    producao,
    direcao
  ) {
    if (abriu) {
      setAbri(false);
      setImgUrl("");
      setLink("");
      setcardTitle("");
    } else {
      setAbri(true);
      setImgUrl(url);
      setLink(link);
      setcardTitle(title);
      setsinopsia(sinopsia);
      setartista(artista);
      setcategoria(categoria);
      setclassificacao(classificacao);
      setdescricaoClassifica(descricaoClassifica);
      setelenco(elenco);
      setproducao(producao);
      setdirecao(direcao);
    }
  }

  const changeVisible = (value) => {
    if (value === "off") {
      setVisible({
        status: false,
        text: value,
      });
    } else {
      setVisible({
        status: true,
        text: value,
      });
    }
  };

  const Horarios = () => {
    if (visible.text === "Enter") {
      return <Auth visible={visible} changeVisible={changeVisible} />;
    }
    if (visible.text === "Register") {
      return <RegisterPopup visible={visible} changeVisible={changeVisible} />;
    }
    if (visible.text === "Info") {
      return <Info visible={visible} changeVisible={changeVisible} />;
    }
  };

  const history = useHistory();

  const logado = localStorage.getItem("logado");

  console.log("logado" + logado);

  function radiovalue() {
    const datachek = document.getElementById("huey").value;
    console.log(datachek);
    setchecado(true);
  }

  function proximo() {
    if (checado) {
      if (logado == "true") {
        let title = props.titulo;
        let schedule = document.getElementById("horario").innerHTML;
        history.push(`/cart?id=` + title + `&schedule=` + schedule);
      } else {
        history.push(`/LoginCart/id`);
      }
    } else {
      alert("Escolha um horario");
    }
  }
  return (
    <>
      <Header />
      <S.Container>
        <S.cardZinho>
          <S.cardzinhoItem>
            <S.Thumbnail src={props.img} />
            <S.cardzinhoItemSobre>
              <S.Span size="16">{props.titulo}</S.Span>
              <S.Span size="14">{props.artista}</S.Span>
              <br />
              <S.Span size="13">Data</S.Span>
              <S.Span size="13">horario</S.Span>
              <S.Span size="13">BacksTage</S.Span>
            </S.cardzinhoItemSobre>
          </S.cardzinhoItem>
        </S.cardZinho>
        <S.Horarios>
          <Section title="Selecione um horario disponievel">
            <HorizontalList id="canais">
              {ListaTeste.map((card, index) => {
                return (
                  <SwiperSlide key={String(index)}>
                    {card === "" ? (
                      <Card noContent />
                    ) : (
                      <S.Card>
                        <S.data>
                          <S.Span size="20">
                            {" "}
                            Hoje <br />
                            17/09
                          </S.Span>
                        </S.data>

                        <S.dia>
                          <input
                            className="radio"
                            onChange={radiovalue}
                            type="radio"
                            id="huey"
                            name="drone"
                            value={"17:50"}
                          />
                          <S.Span size="17" id="horario">
                            19:30
                          </S.Span>
                          <S.Span size="17" cor="#FDCC12">
                            R$17,50
                          </S.Span>
                          <S.Span></S.Span>
                        </S.dia>
                      </S.Card>
                    )}
                  </SwiperSlide>
                );
              })}
            </HorizontalList>
          </Section>
        </S.Horarios>
      </S.Container>
      <S.Buttons>
        <S.Button className="btn" onClick={() => props.voltar()}>
          <S.Span size="20" cor="#FFF" className="m-5">
            {" "}
            <i className="bi bi-chevron-left"></i>Voltar{" "}
          </S.Span>
        </S.Button>{" "}
        <S.ButtonContinuar
          className="btn btn-warning m-5"
          onClick={() => proximo()}
        >
          {" "}
          Continuar
        </S.ButtonContinuar>
      </S.Buttons>
      <Footer />
    </>
  );
}

export default Horarios;
