import React, { useCallback, useContext } from "react";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import * as S from "./styles";
import app from "../../auth/config"
import { AuthContext } from "../../auth/AuthContext"
import { useHistory } from "react-router-dom";
import './style.css'

function LoginCart(history) {
  const handleLogin = useCallback(
    async event => {
      event.preventDefault();
      const { email, password } = event.target.elements;
      try {
        await app
          .auth()
          .signInWithEmailAndPassword(email.value, password.value);
        history.push("/");
      } catch (error) {
        if (error == "TypeError: Cannot read property 'push' of undefined") {
          alert("Usuário ou senha inválido!")
        } else {

        }
      }
    },
    [history]
  );

  const asd = useHistory();
  const { currentUser } = useContext(AuthContext);

  if (currentUser) {
    localStorage.setItem("logado",'true');
    asd.push(`/cart`)
    return (
      <>

        <h1>Usuário Logado!</h1>
        <button onClick={() => app.auth().signOut()}>Sign out</button>

      </>);
  } else {

  }




  return (


    <>

      <Header />
      <S.Container>
        <S.ContainerItem className="pt-5"><S.Span size="25">Minha conta</S.Span></S.ContainerItem>
        <form onSubmit={handleLogin}>
          <S.ContainerItem>
            <input className="ipt" name="email" type="email" placeholder="Email" />

          </S.ContainerItem>
          <S.ContainerItem>

            <input className="ipt" name="password" type="password" placeholder="Senha" />
          </S.ContainerItem>
          <S.ContainerItem className="pt-3">
            <S.Entrar className="btn m-2 ">Entrar</S.Entrar>
            <S.Cadastro className="btn m-2" onClick={() => asd.push("/Cadastro/idespetaculo")}> Cadastre-se</S.Cadastro>
          </S.ContainerItem>
        </form>

        <S.Span size="15" className="p-5" onClick={() => asd.push('/EsqueciSenha')}>Esqueceu a senha?</S.Span>


        <S.Facebook> <i class="bi bi-facebook "></i> Entrar com Facebook</S.Facebook>
        <S.Google><i class="bi bi-google"></i> Entrar com Google</S.Google>
      </S.Container>

      <Footer />



    </>


  );
}

export default LoginCart;
